import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NbIconComponent } from '@nebular/theme';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';
import { S3Service } from '../../../services/s3.service';

@Component({
  selector: 'ngx-editar-sector',
  templateUrl: './editar-sector.component.html',
  styleUrls: ['./editar-sector.component.scss'],
})
export class EditarSectorComponent implements OnInit {
  readonly API_SECTOR = 'api/sector';

  loading = true;
  sector: any;
  selectedFile: any;
  iconChanged = false;
  sectorId = '';

  sectorForm = this.fb.group({
    nombre: ['', Validators.required],
    icono: [null],
    descripcion: ['', Validators.required],
  });

  constructor(
    private fb: FormBuilder,
    private s3: S3Service,
    private generalService: GeneralService,
    private mainService: MainService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.sectorId = this.activatedRoute.snapshot.paramMap.get('id');
    this.populateFormSectorById(this.sectorId);
  }

  populateFormSectorById(id: string) {
    this.mainService.get({ api: `${this.API_SECTOR}/${id}` }).then((value) => {
      this.sector = value;
      this.sectorForm.patchValue({
        nombre: value.nombre,
        descripcion: value.descripcion[0],
      });
      this.loading = false;
    });
  }

  public onChangeMedia({ event }) {
    if (event.target.files.length > 0) {
      this.selectedFile = {
        file: event.target.files[0],
        type: event.target.files[0].type.split('/')[0],
      };
      this.iconChanged = true;
    }
  }

  public openFileSelector() {
    document.getElementById('media').click();
  }

  onSubmit() {
    this.loading = true;
    let editedSector = this.sectorForm.value;

    const s3UploadFileCallback = (err, data) => {
      if (err) {
        alert('No fue posible subir el archivo, por favor intenta  más tarde');
        this.loading = false;
      } else {
        editedSector.icono = data.Location;
        this.updateSector(editedSector);
      }
    };

    if (this.iconChanged) {
      const key = `sector/${this.s3.generateKey()}.${this.selectedFile.file.name.split('.')[1]}`;
      this.s3.uploadFile({ file: this.selectedFile.file, key, callback: s3UploadFileCallback });
    } else {
      delete editedSector.icono;
      this.updateSector(editedSector);
    }
  }

  updateSector(editedSector) {
    this.mainService
      .put({
        api: `${this.API_SECTOR}/${this.sectorId}`,
        data: editedSector,
        hide404: true,
      })
      .then(() => {
        this.generalService.handleSuccess({
          text: 'Sector editado exitosamente.',
        });
        this.goBack();
      });
  }

  public goBack() {
    this.generalService.goBack();
  }
}
