import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "../../../services/general.service";
import { MainService } from "../../../services/main.service";
import { S3Service } from "../../../services/s3.service";

@Component({
	selector: "ngx-productos-crear",
	templateUrl: "./productos-crear.component.html",
	styleUrls: ["./productos-crear.component.scss"],
})
export class ProductosCrearComponent implements OnInit {
	/** API URLS */
	private readonly API_SECTOR: string = "api/sector";
	private readonly API_EMPRESA: string = "api/empresa";
	private readonly API_PRODUCTO: string = "api/producto";

	/** Todos los sectores de la producto */
	public sectores = [];

	/** producto model binding */
	public producto: any = { descuento: false };

	/** Loader */
	public loading = false;

	/** selected media file */
	public selectedFile;

	/** Boolean for edit screen */
	public edit = false;

	constructor(
		private mainService: MainService,
		private generalService: GeneralService,
		private s3: S3Service,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit(): void {
		const id = this.activatedRoute.snapshot.paramMap.get("id");
		if (id) {
			this.edit = true;
			this.fetchProduct({ id });
		}
		this.getSectores();
	}

	/**
	 * Fetchs sectores from backend
	 */
	public getSectores() {
		this.loading = true;
		this.mainService
			.get({ api: this.API_SECTOR })
			.then((response) => {
				this.sectores = response as [];
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Fetchs producto from backend
	 * @param id product id
	 */
	fetchProduct({ id }): void {
		this.loading = true;
		this.mainService
			.get({ api: `${this.API_PRODUCTO}/${id}` })
			.then((value) => {
				this.producto = value as {};
				this.producto.sector = this.producto.sector._id;
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Navigates back
	 */
	public goBack() {
		this.generalService.goBack();
	}

	/**
	 * Opens file selector for media
	 */
	public openFileSelector() {
		document.getElementById("media").click();
	}

	/**
	 * Handles on submit and registers the producto in backend
	 */
	public onSubmit() {
		this.loading = true;
		const callback = (err, data) => {
			if (err) {
				// If there is an error alert the user
				alert("No fue posible subir el archivo, por favor intente más tarde");
				this.loading = false;
			} else {
				const imgUrl = data.Location;
				this.createProducto({ media: imgUrl });
			}
		};
		if (this.selectedFile && this.selectedFile.file) {
			this.uploadFile({ file: this.selectedFile.file, callback });
		} else {
			this.createProducto({ media: this.producto.foto });
		}
	}

	/**
	 * Listens on change of select file
	 * @param param0 with event of the input
	 */
	public onChangeMedia({ event }) {
		if (event.target.files.length > 0) {
			this.selectedFile = {
				file: event.target.files[0],
				type: event.target.files[0].type.split("/")[0],
			};
		}
	}

	/**
	 * Creates producto in backend
	 * @param param0 object with url param containing media url
	 */
	createProducto({ media }: { media: string }) {
		if (
			!media ||
			!this.producto.descripcion ||
			!this.producto.titulo ||
			!this.producto.sector ||
			!this.producto.precio
		) {
			this.loading = false;
			this.generalService.handleError({
				error: "Por favor llena todos los campos",
				titulo: "Error",
			});
		} else {
			this.producto["foto"] = media;
			if (this.edit) {
				this.mainService
					.put({
						api: `${this.API_PRODUCTO}/${this.producto._id}`,
						data: this.producto,
					})
					.then((response) => {
						if (response && response._id) {
							this.generalService.handleSuccess({ text: "Producto editado" });
							this.goBack();
						} else {
							this.generalService.handleError({
								error: "Hubo un error al editar el producto",
								titulo: "Error",
							});
						}
					})
					.finally(() => {
						this.loading = false;
					});
			} else {
				this.mainService
					.post({ api: this.API_PRODUCTO, data: this.producto })
					.then((response) => {
						if (response && response._id) {
							this.generalService.handleSuccess({ text: "Producto creado" });
							this.goBack();
						} else {
							this.generalService.handleError({
								error: "Hubo un error al crear el producto",
								titulo: "Error",
							});
						}
					})
					.finally(() => {
						this.loading = false;
					});
			}
		}
	}

	/** Toggles higlight depending on the current state of the post */
	toggleDescuento(): void {
		this.producto.descuento = !this.producto.descuento;
	}

	/**
	 * Uploads pictures to S3 in a folder
	 * @param file: File object to upload, callback function to execute
	 */
	async uploadFile({ file, callback }) {
		const key = `producto/${this.producto.sector}/${this.s3.generateKey()}.${
			file.name.split(".")[1]
		}`;
		await this.s3.uploadFile({ file, key, callback });
	}
}
