import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";
import { S3Service } from "../../services/s3.service";

@Component({
	selector: "ngx-publicaciones-crear",
	templateUrl: "./publicaciones-crear.component.html",
	styleUrls: ["./publicaciones-crear.component.scss"],
})
export class PublicacionesCrearComponent implements OnInit {
	/** API URLS */
	private readonly API_SECTOR: string = "api/sector";
	private readonly API_EMPRESA: string = "api/empresa";
	private readonly API_PUBLICACION: string = "api/publicacion";

	/** Todos los sectores de la publicacion */
	public sectores = [];

	/** Publicacion model binding */
	public publicacion: any = { nueva: false };

	/** Loader */
	public loading = false;

	/** selected media file */
	public selectedFile;

	/** Array of empresas */
	public empresas: any = [];

	/** Boolean for superAdmin user */
	public isSuperAdmin = false;

	public empresa: any;

	/** Boolean to view if it is edit */
	public edit = false;

	constructor(
		private mainService: MainService,
		private generalService: GeneralService,
		private s3: S3Service,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit(): void {
		this.isSuperAdmin =
			JSON.parse(localStorage.getItem("_user")).tipo === "superadministrador";
		if (!this.isSuperAdmin) {
			this.empresa = JSON.parse(localStorage.getItem("_user")).empresa;
		}
		this.getEmpresas();
		this.getSectores();
	}

	/**
	 * Fetchs publicacion from backend
	 * @param uid user id
	 */
	fetchPost({ id }): void {
		this.loading = true;
		this.mainService
			.get({ api: `${this.API_PUBLICACION}/${id}` })
			.then((value) => {
				this.publicacion = value as {};
				this.publicacion.sector = this.publicacion.sector._id;
				this.empresa = this.empresas.find(
					(emp) => emp._id == this.publicacion.empresa._id
				);
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Fetchs empresas from backend
	 */
	public getEmpresas() {
		this.loading = true;
		this.mainService
			.get({ api: this.API_EMPRESA })
			.then((response) => {
				const id = this.activatedRoute.snapshot.paramMap.get("id");
				this.empresas = response as [];
				if (id) {
					this.edit = true;
					this.fetchPost({ id });
				}
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Fetchs sectores from backend
	 */
	public getSectores() {
		this.loading = true;
		this.mainService
			.get({ api: this.API_SECTOR })
			.then((response) => {
				this.sectores = response as [];
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Navigates back
	 */
	public goBack() {
		this.generalService.goBack();
	}

	/**
	 * Opens file selector for media
	 */
	public openFileSelector() {
		document.getElementById("media").click();
	}

	/**
	 * Handles on submit and registers the publicacion in backend
	 */
	public onSubmit() {
		this.loading = true;
		const callback = (err, data) => {
			if (err) {
				// If there is an error alert the user
				alert("No fue posible subir el archivo, por favor intente más tarde");
				this.loading = false;
			} else {
				const imgUrl = data.Location;
				this.createPublicacion({ media: imgUrl });
			}
		};
		if (this.selectedFile && this.selectedFile.file) {
			this.uploadFile({ file: this.selectedFile.file, callback });
		} else {
			this.createPublicacion({
				media: this.publicacion.foto || this.publicacion.video,
			});
		}
	}

	/**
	 * Listens on change of select file
	 * @param param0 with event of the input
	 */
	public onChangeMedia({ event }) {
		if (event.target.files.length > 0) {
			this.selectedFile = {
				file: event.target.files[0],
				type: event.target.files[0].type.split("/")[0],
			};
		}
	}

	/**
	 * Creates publication in backend
	 * @param param0 object with url param containing media url
	 */
	createPublicacion({ media }: { media: string }) {
		if (
			!media ||
			!this.publicacion.texto ||
			!this.publicacion.titulo ||
			!this.publicacion.link ||
			!this.publicacion.sector ||
			!this.empresa
		) {
			this.loading = false;
			this.generalService.handleError({
				error: "Por favor llena todos los campos",
				titulo: "Error",
			});
		} else {
			this.publicacion["empresa"] = this.empresa._id || "";
			if (this.selectedFile && this.selectedFile.type) {
				if (this.selectedFile.type === "image") {
					this.publicacion["foto"] = media;
				} else {
					this.publicacion["video"] = media;
				}
			}
			if (this.edit) {
				this.mainService
					.put({
						api: `${this.API_PUBLICACION}/${this.publicacion._id}`,
						data: this.publicacion,
					})
					.then((response) => {
						if (response && response._id) {
							this.generalService.handleSuccess({ text: "Pubicación editada" });
							this.goBack();
						} else {
							this.generalService.handleError({
								error: "Hubo un error al editar la publicación",
								titulo: "Error",
							});
						}
					})
					.finally(() => {
						this.loading = false;
					});
			} else {
				this.mainService
					.post({ api: this.API_PUBLICACION, data: this.publicacion })
					.then((response) => {
						if (response && response._id) {
							this.generalService.handleSuccess({ text: "Pubicación creada" });
							this.goBack();
						} else {
							this.generalService.handleError({
								error: "Hubo un error al crear la publicación",
								titulo: "Error",
							});
						}
					})
					.finally(() => {
						this.loading = false;
					});
			}
		}
	}

	/**
	 * Uploads pictures to S3 in a folder
	 * @param file: File object to upload, callback function to execute
	 */
	async uploadFile({ file, callback }) {
		const empresaName = this.empresa.nombre.replace(" ", "-");
		const key = `empresa/${empresaName}/${this.s3.generateKey()}.${
			file.name.split(".")[1]
		}`;
		await this.s3.uploadFile({ file, key, callback });
	}

	/** Toggles higlight depending on the current state of the post */
	toggleNueva(): void {
		this.publicacion.nueva = !this.publicacion.nueva;
	}
}
