import { Component, OnInit } from '@angular/core';
import { MainService } from '../../services/main.service';
import { GeneralService } from '../../services/general.service';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'ngx-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
  private readonly API_SECTOR: string = 'api/sector';
  private readonly API_USUARIOS: string = 'api/user';

  user: any;
  empresaId: string;
  onEditEmail = false;
  onEditPassword = false;
  sectoresPopulated: any;
  sectores = [];

  updateEmailForm = this.fb.group({
    email: [{ value: '', disabled: true }, [Validators.required, Validators.email]],
  });

  updatePasswordForm = this.fb.group({
    password: ['', Validators.required],
    passwordNueva: ['', [Validators.required, Validators.minLength(6)]],
  });

  constructor(
    private mainService: MainService,
    private generalService: GeneralService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder
  ) {}

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('_user'));

    this.getUserFromServer(this.user._id);
  }

  /**
   * Fetchs sectores from backend
   */

  getUserFromServer(id) {
    this.mainService.get({ api: `${this.API_USUARIOS}/${id}` }).then((value) => {
      this.user = value;

      if (this.user.empresa) {
        this.getSectores(this.user.empresa.sectores);
      }

      this.updateEmailForm.setValue({
        email: value.email,
      });
    });
  }

  public getSectores(sectoresArr) {
    this.mainService.get({ api: this.API_SECTOR }).then((response) => {
      this.sectores = response as [];
      this.sectoresPopulated = sectoresArr.map((value) => this.sectores.find((sector) => sector._id === value));
    });
  }

  onSubmitEmailUpdate() {
    console.log('holongos');
  }

  editEmpresa() {
    this.generalService.routerNavigateTo({
      path: `index/empresas/editar/${this.user.empresa._id}`,
    });
  }

  toggleEdit() {
    this.onEditEmail = !this.onEditEmail;
    this.onEditEmail
      ? this.updateEmailForm.controls['email'].enable()
      : this.updateEmailForm.controls['email'].disable();

    this.updateEmailForm.setValue({
      email: this.user.email,
    });
  }

  toggleEditPassword() {
    this.onEditPassword = !this.onEditPassword;
  }
}
