import { Component, OnChanges, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LocalDataSource } from 'ng2-smart-table';
import { Observable } from 'rxjs';
import { GeneralService } from '../services/general.service';
import { MainService } from '../services/main.service';
import { NbDialogRef, NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-consumer-detail',
  templateUrl: './consumer-detail.component.html',
  styleUrls: ['./consumer-detail.component.scss'],
})
export class ConsumerDetailComponent implements OnInit {
  /** API URLS */
  readonly API_USUARIO = 'api/user';
  readonly API_RECOMPENSA_BY_USER = 'api/recompensa/user';

  loading: boolean = true;
  consumer = {};
  interests = [];
  source: LocalDataSource;
  totalCredits = 0;

  /* Table settings */
  settings = {
    noDataMessage: 'Ningún elemento encontrado',
    actions: false,
    columns: {
      interests: {
        filter: false,
        title: 'Intereses',
        width: '65%',
        valuePrepareFunction: (value: string) => {
          return value.charAt(0).toLocaleUpperCase() + value.substr(1).toLocaleLowerCase();
        },
      },
      ranking: {
        title: 'Ranking',
        filter: false,
        width: '15%',
      },
      credits: {
        title: 'Créditos',
        filter: false,
        width: '15%',
        valuePrepareFunction: (value) => {
          return value.toLocaleString('en-US', {
            style: 'currency',
            currency: 'USD',
          });
        },
      },
    },
  };

  constructor(
    private mainService: MainService,
    private activatedRoute: ActivatedRoute,
    private dialogService: NbDialogService,
    private generalService: GeneralService
  ) {}

  ngOnInit() {
    const userid = this.activatedRoute.snapshot.paramMap.get('user');
    this.source = new LocalDataSource();
    this.getUser({ uid: userid });
    this.getInterest({ uid: userid });
  }

  getUser({ uid }) {
    this.mainService
      .get({ api: `${this.API_USUARIO}/${uid}` })
      .then((value) => {
        this.consumer = value;
      })
      .finally(() => (this.loading = false));
  }

  getInterest({ uid }) {
    this.mainService.get({ api: `${this.API_RECOMPENSA_BY_USER}/${uid}` }).then((value) => {
      if (value.length > 0) {
        this.interests = value.map((element) => {
          return {
            interests: element.sector ? element.sector.nombre : 'null',
            ranking: element.ranking,
            credits: element.creditos,
            id: element._id,
          };
        });

        /** total credits math */
        this.totalCredits = this.interests.reduce((total, currentValue) => {
          return total + currentValue.credits;
        }, 0);

        this.source.load(this.interests);
      }
    });
  }

  openConfirmationDialog(dialog: TemplateRef<any>) {
    this.dialogService.open(dialog);
  }

  onDelete(ref) {
    this.mainService
      .delete({
        api: `${this.API_USUARIO}/${this.activatedRoute.snapshot.paramMap.get('user')}`,
      })
      .then((res) => {
        ref.close();
        this.goBack();
      });
  }

  closeDialog(ref) {
    ref.close();
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }
}
