import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { GeneralService } from '../../services/general.service';
import { MainService } from '../../services/main.service';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-destacadas',
  templateUrl: './destacadas.component.html',
  styleUrls: ['./destacadas.component.scss'],
})
export class DestacadasComponent implements OnInit {
  readonly API_DESTACADOS = 'api/destacado';
  /** Arrya with destacados */
  public destacados: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService,
    private dialogService: NbDialogService
  ) {}

  ngOnInit(): void {
    this.getDestacados();
    this.setSettings();
  }

  /**
   * Gets all publicaciones
   */
  getDestacados() {
    this.mainService.get({ api: 'api/destacado' }).then((res) => {
      this.destacados = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.destacados);
    });
  }

  deletePost(ref, id) {
    this.mainService
      .delete({
        api: `${this.API_DESTACADOS}/${id}`,
      })
      .then(() => {
        ref.close();
        this.getDestacados();
      });
  }

  onCustomAction({ event, dialog }): void {
    switch (event.action) {
      case 'ver':
        this.generalService.routerNavigateTo({
          path: `/index/mvp/destacados/ver/${event.data._id}`,
        });
        break;
      case 'eliminar':
        this.dialogService.open(dialog, {
          context: {
            titulo: event.data.titulo,
            id: event.data._id,
          },
        });
        break;
      default:
        break;
    }
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/destacados/crear`,
    });
  }

  /**
   * Attributes in publicaciones for table
   */
  public setAttributes() {
    this.destacados.forEach((element) => {
      element['nombreSeccion'] = element.seccion ? element.seccion.nombre : 'N/A';
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'ver',
            title: '<i class="fa fa-eye" title="Ver post destacado"></i>',
          },
          {
            name: 'eliminar',
            title: '<i class="fa fa-trash" title="Eliminar post destacado"></i>',
          },
        ],
      },
      columns: {
        titulo: { title: 'Título' },
        nombreSeccion: { title: 'Seccion' },
        texto: { title: 'Texto' },
        likes: {
          title: 'Likes',
          valuePrepareFunction(value) {
            return value.length;
          },
        },
      },
      mode: 'external',
    };
  }
}
