import { Component, OnInit } from '@angular/core';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';
import { S3Service } from '../../../services/s3.service';

@Component({
  selector: 'ngx-destacado-crear',
  templateUrl: './destacado-crear.component.html',
  styleUrls: ['./destacado-crear.component.scss'],
})
export class DestacadoCrearComponent implements OnInit {
  /** API URLS */
  private readonly API_SECCION: string = 'api/seccion';
  private readonly API_DESTACADO: string = 'api/destacado';

  /** Todos los secciones de la destacado */
  public secciones = [];

  /** Destacado model binding */
  public destacado: any = {};

  /** Loader */
  public loading = false;

  /** selected media file */
  public selectedFile;

  /** Array of empresas */
  public empresas: any = [];

  /** Boolean for superAdmin user */
  public isSuperAdmin = false;

  public empresa: any;

  constructor(
    private mainService: MainService, 
    private generalService: GeneralService, 
    private s3: S3Service) {}

  ngOnInit(): void {
    this.isSuperAdmin = JSON.parse(localStorage.getItem('_user')).tipo === 'superadministrador';
    this.getSecciones();
  }

  /**
   * Fetchs secciones from backend
   */
  public getSecciones() {
    this.loading = true;
    this.mainService
      .get({ api: this.API_SECCION })
      .then((response) => {
        this.secciones = response as [];
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Navigates back
   */
  public goBack() {
    this.generalService.goBack();
  }

  /**
   * Opens file selector for media
   */
  public openFileSelector() {
    document.getElementById('media').click();
  }

  /**
   * Handles on submit and registers the destacado in backend
   */
  public onSubmit() {
    if (!this.selectedFile) {
      this.generalService.handleError({
        error: 'Selecciona el contenido multimedia',
        titulo: 'Error',
      });
      return false;
    }
    this.loading = true;
    const callback = (err, data) => {
      if (err) {
        // If there is an error alert the user
        alert('No fue posible subir el archivo, por favor intente más tarde');
        this.loading = false;
      } else {
        const imgUrl = data.Location;
        this.createDestacado({ media: imgUrl });
      }
    };
    this.uploadFile({ file: this.selectedFile.file, callback });
  }

  /**
   * Listens on change of select file
   * @param param0 with event of the input
   */
  public onChangeMedia({ event }) {
    if (event.target.files.length > 0) {
      this.selectedFile = {
        file: event.target.files[0],
        type: event.target.files[0].type.split('/')[0],
      };
    }
  }

  /**
   * Creates publication in backend
   * @param param0 object with url param containing media url
   */
  createDestacado({ media }: { media: string }) {
    if (!media || !this.destacado.texto || !this.destacado.titulo || !this.destacado.seccion) {
      this.loading = false;
      this.generalService.handleError({
        error: 'Por favor llena todos los campos',
        titulo: 'Error',
      });
    } else {
      if (this.selectedFile.type === 'image') {
        this.destacado['foto'] = media;
      } else {
        this.destacado['video'] = media;
      }
      this.mainService
        .post({ api: this.API_DESTACADO, data: this.destacado })
        .then((response) => {
          if (response && response._id) {
            this.generalService.handleSuccess({
              text: 'Post destacado creado',
            });
            this.goBack();
          } else {
            this.generalService.handleError({
              error: 'Hubo un error al crear el post',
              titulo: 'Error',
            });
          }
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }

  /**
   * Uploads pictures to S3 in a folder
   * @param file: File object to upload, callback function to execute
   */
  async uploadFile({ file, callback }) {
    const key = `destacado/${this.s3.generateKey()}.${file.name.split('.')[1]}`;
    await this.s3.uploadFile({ file, key, callback });
  }
}
