import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';
import { NbDialogRef, NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-secciones',
  templateUrl: './secciones.component.html',
  styleUrls: ['./secciones.component.scss'],
})
export class SeccionesComponent implements OnInit {
  /**  API URLS */
  readonly API_SECCION = 'api/seccion';
  /** Arrya with secciones */
  public secciones: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService,
    private dialogService: NbDialogService
  ) {}

  ngOnInit(): void {
    this.getSecciones();
    this.setSettings();
  }

  /**
   * Gets all publicaciones
   */
  public getSecciones() {
    this.mainService.get({ api: this.API_SECCION }).then((res) => {
      /** Quitar categoria de nuevos agregada en el backend */
      res.pop();

      /** Asignar resto de secciones */
      this.secciones = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.secciones);
    });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event, deleteDialog }): void {
    switch (event.action) {
      case 'editar':
        this.generalService.routerNavigateTo({
          path: `/index/mvp/destacados/seccion/editar/${event.data._id}`,
        });
        break;
      case 'eliminar':
        this.dialogService.open(deleteDialog, {
          context: {
            nombre: event.data.nombre,
            id: event.data._id,
          },
        });
        break;
      default:
        break;
    }
  }

  deleteSection(ref, id) {
    this.mainService.delete({ api: `${this.API_SECCION}/${id}` }).then((res) => {
      ref.close();
      this.getSecciones();
    });
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/destacados/seccion/crear`,
    });
  }

  /**
   * Attributes in publicaciones for table
   */
  public setAttributes() {
    this.secciones.forEach((element) => {
      element['nombreSeccion'] = element.seccion ? element.seccion.nombre : 'N/A';
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'editar',
            title: '<i class="fa fa-edit" title="Editar sección destacada"></i>',
          },
          {
            name: 'eliminar',
            title: '<i class="fa fa-trash" title="Eliminar sección destacada"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: 'Título' },
        descripcion: { title: 'Descripción' },
        icono: {
          title: 'Icono',
          type: 'html',
          valuePrepareFunction: (images: string) => {
            return `<img class='table-thumbnail-img' src='${images}'/>`;
          },
        },
      },
      mode: 'external',
    };
  }
}
