import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { MainService } from '../services/main.service';
import { NbIconLibraries } from '@nebular/theme';

@Component({
  selector: 'ngx-usuarios',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.scss'],
})
export class UsuariosComponent implements OnInit {
  /** API URLS */
  readonly API_USUARIO = 'api/user';
  readonly API_USERS_COUNT = 'api/user/count';
  readonly API_USERS_ACTIVE_COUNT = 'api/user/active/count';

  /** Controller for loading */
  loading = false;
  kpiLoading = false;

  /** Kpi Cards content */
  activeUsers = 0;
  userCount = 0;

  /** Smart table config */
  consumers = {
    settings: {
      noDataMessage: 'Ningún registro encontrado.',
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'ver-consumidor',
            title: '<i title="Editar usuario" class="fas fa-pen"></i>',
          },
          {
            name: 'borrar-consumidor',
            title: '<i title="Eliminar usuario" class="fas fa-trash"></i>',
          },
        ],
      },
      columns: {
        // nombre: {
        //   title: "Nombre (s)",
        //   valuePrepareFunction: (value: string) => {
        //     return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) =>
        //       letter.toUpperCase()
        //     );
        //   },
        // },
        // apellido: {
        //   title: "Apellido",
        //   valuePrepareFunction: (value: string) => {
        //     return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) =>
        //       letter.toUpperCase()
        //     );
        //   },
        // },
        email: { title: 'Email', width: '90%' },
      },
      mode: 'external',
    },
    source: [],
  };

  empresas = {
    settings: {
      noDataMessage: 'Ningún registro encontrado.',
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        width: '100px',
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'ver-empresa',
            title: '<i title="Editar empresa" class="fas fa-pen"></i>',
          },
          {
            name: 'borrar-empresa',
            title: '<i title="Eliminar empresa" class="fas fa-trash"></i>',
          },
        ],
      },
      columns: {
        nombre: {
          title: 'Nombre (s)',
          width: '15%',
          valuePrepareFunction: (value: string) => {
            return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) => letter.toUpperCase());
          },
        },
        apellido: {
          title: 'Apellido',
          width: '15%',
          valuePrepareFunction: (value: string) => {
            return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) => letter.toUpperCase());
          },
        },
        email: { title: 'Email' },
        nombreEmpresa: { title: 'Empresa' },
      },
      mode: 'external',
    },
    source: [],
  };

  admin = {
    settings: {
      noDataMessage: 'Ningún registro encontrado.',
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        width: '100px',
        edit: false,
        delete: false,
        custom: [
          {
            name: 'deshabilitar-admin',
            title: '<i title="Deshabilitar administrador" class="fas fa-user-slash"></i>',
          },
        ],
      },
      columns: {
        nombre: {
          title: 'Nombre (s)',
          valuePrepareFunction: (value: string) => {
            return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) => letter.toUpperCase());
          },
        },
        apellido: {
          title: 'Apellido',
          valuePrepareFunction: (value: string) => {
            return value.replace(/(^\w{1})|(\s+\w{1})/g, (letter) => letter.toUpperCase());
          },
        },
        email: { title: 'Email', width: '30%' },
      },
      mode: 'external',
    },
    source: [],
  };

  constructor(private mainService: MainService, private iconLibraries: NbIconLibraries) {
    this.iconLibraries.registerFontPack('font-awesome', {
      iconClassPrefix: 'fa',
      packClass: 'fas',
    });
  }

  ngOnInit(): void {
    this.refresh();
  }

  refresh() {
    this.fetchKpi();
    this.fetchUsers();
  }

  fetchUsers() {
    this.loading = true;
    this.mainService
      .get({ api: this.API_USUARIO, hide404: true })
      .then((value) => {
        let consumers = value.filter((user) => {
          return user.tipo === 'consumidor';
        });
        let empresas = value.filter((user) => {
          return user.tipo === 'administrador';
        });

        empresas = empresas.map((admin) => {
          return { ...admin, nombreEmpresa: admin.empresa.nombre };
        });

        let admin = value.filter((user) => {
          return user.tipo === 'superadministrador';
        });

        this.setUsersTable({
          consumers: consumers,
          empresas: empresas,
          admin: admin,
        });
      })
      .finally(() => {
        this.loading = false;
      });
  }

  fetchKpi() {
    // user count
    this.mainService.get({ api: this.API_USERS_COUNT, hide404: true }).then((value) => {
      this.userCount = value;
    });

    this.mainService.get({ api: this.API_USERS_ACTIVE_COUNT }).then((value) => {
      this.activeUsers = value;
    });
  }

  setUsersTable({ consumers, empresas, admin }) {
    this.consumers.source = consumers;
    this.empresas.source = empresas;
    this.admin.source = admin;
  }
}
