import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { reduce } from 'rxjs/operators';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';

@Component({
  selector: 'ngx-productos-ver',
  templateUrl: './productos-ver.component.html',
  styleUrls: ['./productos-ver.component.scss'],
})
export class ProductosVerComponent implements OnInit {
  /** API urls*/
  readonly API_PRODUCT = 'api/producto';
  readonly API_COMPRAS_PRODUCTO = 'api/compra-producto/producto';

  /** Variable for user information */
  public user: any = {};

  /** Variable for loading state */
  public loading = false;

  /** Variable for product information */
  public producto: any = {};
  public compras: any;

  settingsComprasProducto = {
    actions: false,
    noDataMessage: 'Cargando lista de clientes...',
    columns: {
      email: {
        title: 'Email de comprador',
      },
      calificacionApp: {
        title: 'Ranking',
        filter: false,
      },
      quantity: {
        title: 'Cantidad',
        filter: false,
      },
      total: {
        title: 'Total',
        filter: false,
        valuePrepareFunction: (value) => {
          return Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(value);
        },
      },
    },
  };

  constructor(
    private activatedRoute: ActivatedRoute,
    private mainService: MainService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    const id = this.activatedRoute.snapshot.paramMap.get('id');
    this.user = JSON.parse(localStorage.getItem('_user'));
    this.fetchProduct({ id });
    this.getComprasProducto({ id });
  }

  /**
   * Fetchs producto from backend
   * @param uid user id
   */
  fetchProduct({ id }): void {
    this.loading = true;
    this.mainService
      .get({ api: `${this.API_PRODUCT}/${id}` })
      .then((value) => {
        this.producto = value as {};
      })
      .finally(() => {
        this.loading = false;
      });
  }

  getComprasProducto({ id }) {
    this.mainService.get({ api: `${this.API_COMPRAS_PRODUCTO}/${id}` }).then((value) => {
      console.log(value);
      const reduceObj = [];

      value.forEach((compra) => {
        const findIndexIfBought = reduceObj.findIndex((element) => element._id === compra.usuario._id);
        if (findIndexIfBought >= 0) {
          reduceObj[findIndexIfBought].quantity++;
          reduceObj[findIndexIfBought].total =
            reduceObj[findIndexIfBought].quantity * reduceObj[findIndexIfBought].total;
        } else {
          reduceObj.push({ ...compra.usuario, quantity: 1, total: compra.producto.precio });
        }
      });

      this.compras = reduceObj;
    });
  }

  /** Navigates to edit sreen */
  public onEdit() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/productos/editar/${this.producto._id}`,
    });
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }
}
