import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";
import * as moment from "moment";

@Component({
  selector: "ngx-publicaciones-empresa",
  templateUrl: "./publicaciones-empresa.component.html",
  styleUrls: ["./publicaciones-empresa.component.scss"],
})
export class PublicacionesEmpresaComponent implements OnInit {
  /** API URLS */
  readonly API_EMPRESAS = "api/empresa";
  readonly API_PUBLICACION = "api/publicacion";
  readonly API_POSTS_BY_EMPRESA = "api/publicacion/empresa";

  /** Object empresa */
  empresa: any = {};

  /** Contains all posts */
  publicaciones = [];

  /** Eligible years to filter */
  years = [];

  /** Contains year filtered posts */
  filterPublicaciones = { year: new Date().getFullYear(), publicaciones: [] };

  /** Table loader */
  loading = false;

  /** Table source */
  source = [];

  /** Table settings */
  settings = {
    actions: {
      columnTitle: "Acciones",
      filter: true,
      add: false,
      edit: false,
      delete: false,
      custom: [
        {
          name: "ver",
          title: '<i class="fa fa-search" title="Ver publicación"></i>',
        },
        // {
        //   name: 'destacar',
        //   title: '<i class="far fa-star" title="Destacar"></i>',
        // },
      ],
    },
    columns: {
      titulo: { title: "Publicación", width: "80%" },
    },
    mode: "external",
  };

  chartConfig = {
    data: [],
    showXAxis: true,
    showYAxis: true,
    gradient: false,
    showLegend: false,
    showXAxisLabel: true,
    xAxisLabel: "Meses",
    showYAxisLabel: true,
    yAxisLabel: "# Publicaciones",
    animations: true,
    view: [undefined, 400],
    colorScheme: {
      domain: ["#200081"],
    },
  };

  constructor(private mainService: MainService, private activatedRoute: ActivatedRoute, private generalService: GeneralService) {}

  ngOnInit(): void {
    for (let i = new Date().getFullYear() - 5; i <= new Date().getFullYear(); i++) {
      this.years.push(i);
    }
    const id = this.activatedRoute.snapshot.paramMap.get("empresa");
    this.fetchEmpresaAndPosts(id);
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }

  /**
   * Fetchs empresa and its posts by id
   * @param id of the empresa
   */
  fetchEmpresaAndPosts(id): void {
    this.loading = true;
    this.mainService
      .get({ api: `${this.API_EMPRESAS}/${id}` })
      .then((value) => {
        this.empresa = value;
        this.mainService.get({ api: `${this.API_POSTS_BY_EMPRESA}/${id}` }).then((posts) => {
          this.publicaciones = posts as any[];
          this.source = this.publicaciones;
          this.filterPublicaciones.publicaciones = this.publicaciones;
          this.setUpChart();
        });
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Higlights a post by id
   * @param object with id of post and destacar action
   */
  highlightPost({ id, destacar }: { id: string; destacar: boolean }): void {
    this.loading = true;
    const data = { destacada: destacar };
    this.mainService
      .put({ api: `${this.API_PUBLICACION}/${id}`, data })
      .then((value) => {})
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event }): void {
    if (event.action === "ver") {
      this.generalService.routerNavigateTo({
        path: `index/publicaciones/${event.data._id}`,
      });
    } else if (event.action === "destacar") {
      // this.highlightPost({id: event.data._id, destacar })
    }
  }

  /**
   * Handles on change chart year
   */
  onChangeYear() {
    this.filterPublicaciones.publicaciones = this.publicaciones.filter((element) => {
      const year = parseInt(moment(element.createdAt).format("Y"));
      if (year === this.filterPublicaciones.year) {
        return true;
      } else {
        return false;
      }
    });
    this.setUpChart();
  }

  /**
   * Sets up chart data from filters
   */
  setUpChart() {
    let data = [];
    const currentYear = parseInt(moment().format("Y"));
    if (this.filterPublicaciones.year === currentYear) {
      const currentMonth = parseInt(moment().format("M"));
      for (let i = 1; i <= currentMonth; i++) {
        const obj = {
          value: 0,
          name: "",
        };
        switch (i) {
          case 1:
            obj.name = "Enero";
            break;
          case 2:
            obj.name = "Febrero";
            break;
          case 3:
            obj.name = "Marzo";
            break;
          case 4:
            obj.name = "Abril";
            break;
          case 5:
            obj.name = "Mayo";
            break;
          case 6:
            obj.name = "Junio";
            break;
          case 7:
            obj.name = "Julio";
            break;
          case 8:
            obj.name = "Agosto";
            break;
          case 9:
            obj.name = "Septiembre";
            break;
          case 10:
            obj.name = "Octubre";
            break;
          case 11:
            obj.name = "Noviembre";
            break;
          case 12:
            obj.name = "Diciembre";
            break;
        }
        data.push(obj);
      }
    } else {
      data = [
        {
          name: "Enero",
          value: 0,
        },
        {
          name: "Febrero",
          value: 0,
        },
        {
          name: "Marzo",
          value: 0,
        },
        {
          name: "Abril",
          value: 0,
        },
        {
          name: "Mayo",
          value: 0,
        },
        {
          name: "Junio",
          value: 0,
        },
        {
          name: "Julio",
          value: 0,
        },
        {
          name: "Agosto",
          value: 0,
        },
        {
          name: "Septiembre",
          value: 0,
        },
        {
          name: "Octubre",
          value: 0,
        },
        {
          name: "Noviembre",
          value: 0,
        },
        {
          name: "Diciembre",
          value: 0,
        },
      ];
    }
    try {
      if (this.filterPublicaciones.publicaciones.length > 0) {
        this.filterPublicaciones.publicaciones.forEach((element) => {
          const month = parseInt(moment(element.createdAt).format("M"));
          data[month - 1].value += 1;
        });
      }
      this.chartConfig.data = data;
    } catch (error) {}
  }
}
