import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { GeneralService } from '../../../../services/general.service';
import { MainService } from '../../../../services/main.service';
import { S3Service } from '../../../../services/s3.service';

@Component({
  selector: 'ngx-seccion-editar',
  templateUrl: './seccion-editar.component.html',
  styleUrls: ['./seccion-editar.component.scss'],
})
export class SeccionEditarComponent implements OnInit {
  readonly API_SECCIONES_DESTACADAS = 'api/seccion';

  sectionId: string;
  section: any;
  iconChanged = false;
  selectedFile: any;
  loading = false;

  editSectionForm = this.fb.group({
    icono: [null],
    nombre: ['', Validators.required],
    descripcion: ['', Validators.required],
  });

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef,
    private s3: S3Service,
    private generalService: GeneralService,
    private mainService: MainService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.sectionId = this.activatedRoute.snapshot.paramMap.get('id');
    this.populateSectionFormById(this.sectionId);
  }

  populateSectionFormById(id: string) {
    this.mainService.get({ api: `${this.API_SECCIONES_DESTACADAS}/${id}` }).then((value) => {
      this.section = value;
      this.editSectionForm.patchValue({
        nombre: value.nombre,
        descripcion: value.descripcion,
      });
    });
  }

  onChangeMedia({ event }) {
    if (event.target.files.length > 0) {
      this.selectedFile = {
        file: event.target.files[0],
        type: event.target.files[0].type.split('/')[0],
      };
      this.iconChanged = true;
    }
  }

  public openFileSelector() {
    document.getElementById('media').click();
  }

  onSubmit() {
    this.loading = true;
    let editedSection = this.editSectionForm.value;

    const s3UploadFileCallback = (err, data) => {
      if (err) {
        alert('No fue posible subir el archivo, por favor intenta  más tarde');
        this.loading = false;
      } else {
        editedSection.icono = data.Location;
        this.updateSection(editedSection);
      }
    };

    if (this.iconChanged) {
      const key = `sector/${this.s3.generateKey()}.${this.selectedFile.file.name.split('.')[1]}`;
      this.s3.uploadFile({ file: this.selectedFile.file, key, callback: s3UploadFileCallback });
    } else {
      delete editedSection.icono;
      this.updateSection(editedSection);
    }
  }

  updateSection(editedSection) {
    this.mainService
      .put({
        api: `${this.API_SECCIONES_DESTACADAS}/${this.sectionId}`,
        data: editedSection,
        hide404: true,
      })
      .then(() => {
        this.generalService.handleSuccess({
          text: 'Sección editada exitosamente.',
        });
        this.goBack();
      });
  }

  goBack() {
    this.generalService.goBack();
  }
}
