import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';
import { S3Service } from '../../../services/s3.service';

@Component({
  selector: 'ngx-destacado-editar',
  templateUrl: './destacado-editar.component.html',
  styleUrls: ['./destacado-editar.component.scss'],
})
export class DestacadoEditarComponent implements OnInit {
  /** API URL  */
  private readonly API_DESTACADO: string = 'api/destacado';
  private readonly API_SECCIONES: string = 'api/seccion';

  loading = true;
  postId: string;
  post: any;
  highlightedSections: any[];

  selectedFile: any;
  mediaChanged = false;

  editPostForm = this.fb.group({
    titulo: ['', Validators.required],
    seccion: ['', Validators.required],
    texto: ['', Validators.required],
    video: [null],
    foto: [null],
  });

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef,
    private s3: S3Service,
    private generalService: GeneralService,
    private mainService: MainService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    const postId = this.activatedRoute.snapshot.paramMap.get('id');
    this.postId = postId;
    this.getHighlightedSections();
  }

  onSubmit() {
    this.loading = true;
    let editedPost = this.editPostForm.value;
    const sectionId = this.editPostForm.value.seccion;
    editedPost.seccion = this.highlightedSections.find((element) => element._id === sectionId);

    const s3UploadFileCallback = (err, data) => {
      if (err) {
        alert('No fue posible subir el archivo, por favor intenta  más tarde');
        this.loading = false;
      } else {
        this.post.video ? delete editedPost.video : delete editedPost.foto;
        this.selectedFile.type === 'image' ? (editedPost.foto = data.Location) : (editedPost.video = data.Location);
        this.updateHighlightedPost(editedPost);
      }
    };

    if (this.mediaChanged) {
      const key = `destacado/${this.s3.generateKey()}.${this.selectedFile.file.name.split('.')[1]}`;
      this.s3.uploadFile({ file: this.selectedFile.file, key, callback: s3UploadFileCallback });
    } else {
      delete editedPost.video;
      delete editedPost.foto;
      this.updateHighlightedPost(editedPost);
    }
  }

  getHighlightedSections() {
    this.mainService.get({ api: this.API_SECCIONES }).then((value) => {
      this.getHighlightedPost(this.postId);
      this.highlightedSections = value;
    });
  }

  getHighlightedPost(id: string) {
    this.mainService.get({ api: `${this.API_DESTACADO}/${id}` }).then((value) => {
      this.post = value;
      this.editPostForm.patchValue({
        titulo: value.titulo,
        seccion: value.seccion._id,
        texto: value.texto,
      });
    });
    this.loading = false;
  }

  updateHighlightedPost(updatedPost) {
    this.mainService
      .put({
        api: `${this.API_DESTACADO}/${this.postId}`,
        data: updatedPost,
        hide404: true,
      })
      .then(() => {
        this.generalService.handleSuccess({
          text: 'Publicación destacada editada exitosamente.',
        });
        this.goBack();
      });
  }

  openFileSelector() {
    document.getElementById('media').click();
  }

  onChangeMedia({ event }) {
    if (event.target.files.length > 0) {
      this.selectedFile = {
        file: event.target.files[0],
        type: event.target.files[0].type.split('/')[0],
      };
      this.mediaChanged = true;
    }
  }

  goBack() {
    this.generalService.goBack();
  }
}
