import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NbDialogService } from '@nebular/theme';
import { GeneralService } from '../../../services/general.service';
import { MainService } from '../../../services/main.service';

@Component({
  selector: 'ngx-destacado-ver',
  templateUrl: './destacado-ver.component.html',
  styleUrls: ['./destacado-ver.component.scss'],
})
export class DestacadoVerComponent implements OnInit {
  readonly API_DESTACADOS = 'api/destacado';
  postId: string;
  destacado: any;
  loading = true;

  constructor(
    private mainService: MainService,
    private activatedRoute: ActivatedRoute,
    private dialogService: NbDialogService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.postId = this.activatedRoute.snapshot.paramMap.get('id');
    this.getPost({ postId: this.postId });
  }

  getPost({ postId }) {
    this.mainService
      .get({ api: `${this.API_DESTACADOS}/${postId}` })
      .then((value) => {
        this.destacado = value;

        this.loading = false;
      })
      .catch((e) => console.log(e));
  }

  goBack() {
    this.generalService.goBack();
  }

  onDeleteDialog(dialog: TemplateRef<any>) {
    this.dialogService.open(dialog);
  }

  editPost() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/destacados/editar/${this.postId}`,
    });
  }

  deletePost(ref) {
    this.loading = false;
    this.mainService
      .delete({
        api: `${this.API_DESTACADOS}/${this.postId}`,
      })
      .then((res) => {
        ref.close();
        this.goBack();
      });
  }
}
