import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { MainService } from '../../services/main.service';
import { GeneralService } from '../../services/general.service';

@Component({
  selector: 'ngx-productos',
  templateUrl: './productos.component.html',
  styleUrls: ['./productos.component.scss'],
})
export class ProductosComponent implements OnInit {
  /** Arrya with  */
  public productos: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(private mainService: MainService, private generalService: GeneralService) {}

  ngOnInit(): void {
    this.getproductos();
    this.setSettings();
  }

  /**
   * Gets all productos
   */
  public getproductos() {
    this.mainService.get({ api: 'api/producto' }).then((res) => {
      this.productos = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.productos);
    });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event }): void {
    if (event.action === 'ver') {
      this.generalService.routerNavigateTo({
        path: `index/mvp/productos/${event.data._id}`,
      });
    } else if (event.action === 'destacar') {
      // this.highlightPost({id: event.data._id, destacar })
    }
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/productos/crear`,
    });
  }

  /**
   * Attributes in productos for table
   */
  public setAttributes() {
    this.productos.forEach((element) => {
      element['nombreSector'] = element.sector ? element.sector.nombre : 'N/A';
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'ver',
            title: '<i class="fa fa-search" title="Ver publicación"></i>',
          },
          // {
          //   name: 'destacar',
          //   title: '<i class="far fa-star" title="Destacar"></i>',
          // },
        ],
      },
      columns: {
        titulo: { title: 'Título' },
        compras: { title: 'Compras' },
        nombreSector: { title: 'Sector' },
        precio: {
          title: 'Precio',
          valuePrepareFunction: function (value) {
            return Intl.NumberFormat('en-US', {
              style: 'currency',
              currency: 'USD',
            }).format(value);
          },
        },
        precioDescuento: {
          title: 'Precio descuento',
          valuePrepareFunction: function (value, row) {
            if (value && row.descuento) {
              return Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
              }).format(value);
            } else {
              return 'N/A';
            }
          },
        },
        foto: {
          title: 'Foto',
          type: 'html',
          valuePrepareFunction: (images: string) => {
            return `<img class="product-thumbnail" src='${images}'/>`;
          },
        },
      },
      mode: 'external',
    };
  }
}
