import { Component, OnInit } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";
import { MainService } from "../../services/main.service";
import { GeneralService } from "../../services/general.service";

@Component({
  selector: "ngx-publicaciones",
  templateUrl: "./publicaciones.component.html",
  styleUrls: ["./publicaciones.component.scss"],
})
export class PublicacionesMVPComponent implements OnInit {
  /** Arrya with publicaciones */
  public publicaciones: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.getPublicaciones();
    this.setSettings();
  }

  /**
   * Gets all publicaciones
   */
  public getPublicaciones() {
    this.mainService.get({ api: "api/publicacion" }).then((res) => {
      this.publicaciones = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.publicaciones);
    });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event }): void {
    if (event.action === "ver") {
      this.generalService.routerNavigateTo({
        path: `index/publicaciones/${event.data._id}`,
      });
    } else if (event.action === "destacar") {
      // this.highlightPost({id: event.data._id, destacar })
    }
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/publicaciones/crear`,
    });
  }

  /**
   * Attributes in publicaciones for table
   */
  public setAttributes() {
    this.publicaciones.forEach((element) => {
      element["nombreEmpresa"] = element.empresa
        ? element.empresa.nombre
        : "N/A";
      element["nombreSector"] = element.sector ? element.sector.nombre : "N/A";
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "ver",
            title: '<i class="fa fa-search" title="Ver publicación"></i>',
          },
          // {
          //   name: 'destacar',
          //   title: '<i class="far fa-star" title="Destacar"></i>',
          // },
        ],
      },
      columns: {
        titulo: { title: "Publicación" },
        nombreEmpresa: { title: "Empresa" },
        nombreSector: { title: "Sector" },
      },
      mode: "external",
    };
  }
}
