import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GeneralService } from '../../services/general.service';
import { MainService } from '../../services/main.service';

@Component({
  selector: 'ngx-publicacion-detail',
  templateUrl: './publicacion-detail.component.html',
  styleUrls: ['./publicacion-detail.component.scss'],
})
export class PublicacionDetailComponent implements OnInit {
  /** Publicacion url */
  readonly API_PUBLICACION = 'api/publicacion';

  /** Contains publicacion information*/
  publicacion: any = {};

  /** Contains empresa information */
  empresa: any = {};

  /** Post likes */
  likes: any = 0;

  /** Post likes */
  compartir: any = 0;

  /** Post likes */
  comentarios: any = 0;

  /** Post likes */
  clicks: any = 0;

  /** Current user */
  user;

  /** Loader handler */
  loading = false;

  constructor(
    private generalService: GeneralService,
    private mainService: MainService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    const id = this.activatedRoute.snapshot.paramMap.get('id');
    this.user = JSON.parse(localStorage.getItem('_user'));
    this.fetchPost({ id });
    this.countLikesByPublicacion({ id });
    this.countCompartirByPublicacion({ id });
    this.countClicksByPublicacion({ id });
    this.countComentariosByPublicacion({ id });
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }

  /** Toggles higlight depending on the current state of the post */
  toggleDestacar(): void {
    if (this.publicacion.destacada) {
      this.highlightPost({ id: this.publicacion._id, destacar: false });
    } else {
      this.highlightPost({ id: this.publicacion._id, destacar: true });
    }
  }

  /**
   * Fetchs publicacion from backend
   * @param uid user id
   */
  fetchPost({ id }): void {
    this.loading = true;
    this.mainService
      .get({ api: `${this.API_PUBLICACION}/${id}` })
      .then((value) => {
        this.publicacion = value as {};
        this.empresa = this.publicacion.empresa || {};
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /** Navigates to edit sreen */
  public onEdit() {
    this.generalService.routerNavigateTo({
      path: `index/publicaciones/editar/${this.publicacion._id}`,
    });
  }

  /**
   * Higlights a post by id
   * @param object with id of post and destacar action
   */
  highlightPost({ id, destacar }: { id: string; destacar: boolean }): void {
    this.loading = true;
    const data = { destacada: destacar };
    this.mainService
      .put({ api: `${this.API_PUBLICACION}/${id}`, data })
      .then((value) => {
        this.publicacion = value as {};
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Counts likes by post
   */
  public countLikesByPublicacion({ id }) {
    this.mainService.get({ api: `api/like/publicacion/${id}` }).then((value) => {
      this.likes = value;
    });
  }

  /**
   * Counts comentarios by post
   */
  public countComentariosByPublicacion({ id }) {
    this.mainService.get({ api: `api/comentario/publicacion-contar/${id}` }).then((value) => {
      this.comentarios = value;
    });
  }

  /**
   * Counts compartir by post
   */
  public countCompartirByPublicacion({ id }) {
    this.mainService.get({ api: `api/compartir/publicacion/${id}` }).then((value) => {
      this.compartir = value;
    });
  }

  /**
   * Counts clicks by post
   */
  public countClicksByPublicacion({ id }) {
    this.mainService.get({ api: `api/click/publicacion/${id}` }).then((value) => {
      this.clicks = value;
    });
  }
}
