import { Component, OnInit } from '@angular/core';
import { NbMenuItem } from '@nebular/theme';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'ngx-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss'],
})
export class IndexComponent implements OnInit {
  /** Auth */
  private isSuperAdmin: boolean = false;

  /** Menú de la plataforma */
  public menu: NbMenuItem[] = [
    {
      title: 'CARGANDO',
      group: true,
    },
  ];

  constructor(private authService: AuthService) {}

  ngOnInit() {
    this.isSuperAdmin = JSON.parse(localStorage.getItem('_user')).tipo === 'superadministrador';
    this.setUpMenu();
  }

  /**
   * Construye el menu
   */
  private setUpMenu(): void {
    this.menu = [
      {
        title: 'Inicio',
        icon: 'home-outline',
        link: '/index',
        home: true,
      },
      {
        title: 'Usuarios',
        icon: 'people-outline',
        link: '/index/usuarios',
        hidden: !this.isSuperAdmin,
      },
      {
        title: 'Empresas',
        icon: 'briefcase-outline',
        link: '/index/empresas',
        hidden: !this.isSuperAdmin,
      },
      {
        title: 'Publicaciones',
        icon: 'briefcase-outline',
        link: '/index/publicaciones',
        hidden: this.isSuperAdmin,
      },
      {
        title: 'Notificaciones',
        icon: 'bell-outline',
        link: '/index/notificaciones',
        hidden: this.isSuperAdmin,
      },
      {
        title: 'MVP',
        icon: 'cube-outline',
        hidden: !this.isSuperAdmin,
        children: [
          {
            title: 'Publicaciones',
            icon: 'image-outline',
            link: '/index/mvp/publicaciones',
            hidden: !this.isSuperAdmin,
          },
          {
            title: 'Sectores',
            icon: 'hash-outline',
            link: '/index/mvp/sector',
            hidden: !this.isSuperAdmin,
          },
          {
            title: 'Productos',
            icon: 'gift-outline',
            link: '/index/mvp/productos',
            hidden: !this.isSuperAdmin,
          },
          {
            title: 'Destacados',
            icon: 'star-outline',
            children: [
              {
                title: 'Post',
                link: '/index/mvp/destacados',
                icon: 'image-outline',
                hidden: !this.isSuperAdmin,
              },
              {
                title: 'Secciones',
                link: '/index/mvp/destacados/seccion',
                icon: 'menu-2-outline',
                hidden: !this.isSuperAdmin,
              },
            ],
          },
        ],
      },
    ];
  }
}
