import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  TemplateRef,
} from "@angular/core";
import { NbDialogRef, NbDialogService } from "@nebular/theme";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";

@Component({
  selector: "ngx-usuarios-table",
  templateUrl: "./usuarios-table.component.html",
  styleUrls: ["./usuarios-table.component.scss"],
})
export class UsuariosTableComponent implements OnInit {
  /** Table source */
  @Input() source;

  /** Table settings */
  @Input() settings;

  /** Refresh table method */
  @Output() updateViewEvent: EventEmitter<any> = new EventEmitter();

  readonly API_USUARIO = "api/user";
  readonly API_EMPRESA = "api/empresa";
  readonly API_ADMIN = "api/admin";

  private dialogRef: NbDialogRef<any>;

  constructor(
    private generalService: GeneralService,
    private mainService: MainService,
    private dialogService: NbDialogService
  ) {}

  ngOnInit(): void {}

  /**
   * Handles click on any custom action in the table
   * @param event of the table
   */
  onCustomAction(
    { event },
    deleteUserDialog: TemplateRef<any>,
    deleteEmpresaDialog: TemplateRef<any>,
    disableAdminDialog: TemplateRef<any>
  ): void {
    switch (event.action) {
      case "ver-consumidor":
        this.generalService.routerNavigateTo({
          path: `/index/consumer/${event.data._id}`,
        });
        break;

      case "borrar-consumidor":
        this.dialogRef = this.dialogService.open(deleteUserDialog, {
          context: {
            text: `${event.data.nombre} ${event.data.apellido}`,
            id: event.data._id,
          },
        });
        break;

      case "ver-empresa":
        this.generalService.routerNavigateTo({
          path: `/index/empresa/${event.data.empresa._id}`,
        });
        break;

      case "borrar-empresa":
        this.dialogRef = this.dialogService.open(deleteEmpresaDialog, {
          context: {
            text: `${event.data.empresa.nombre}`,
            id: event.data.empresa._id,
          },
        });
        break;

      case "deshabilitar-admin":
        this.dialogRef = this.dialogService.open(disableAdminDialog, {
          context: {
            text: `${event.data.nombre} ${event.data.apellido}`,
            id: event.data._id,
          },
        });
        break;
        
      default:
        break;
    }
  }

  onDelete(ref, id) {
    this.dialogRef.close();
    this.mainService
      .delete({
        api: `${this.API_USUARIO}/${id}`,
      })
      .finally(() => this.updateViewEvent.emit());
  }

  onDeleteEmpresa(ref, id) {
    this.closeDialog(ref);
    this.mainService
      .delete({
        api: `${this.API_EMPRESA}/${id}`,
      })
      .finally(() => this.updateViewEvent.emit());
  }

  onDeleteAdmin(ref, id) {
    this.closeDialog(ref);
    this.mainService
      .delete({
        api: `${this.API_ADMIN}/${id}`,
      })
      .finally(() => this.updateViewEvent.emit());
  }

  closeDialog(ref) {
    ref.close();
  }
}
