import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ConsumerDetailComponent } from './consumer-detail/consumer-detail.component';
import { EmpresaDetailComponent } from './empresas/empresa-detail/empresa-detail.component';
import { EmpresasComponent } from './empresas/empresas.component';
import { NotificacionesComponent } from './notificaciones/notificaciones.component';
import { ProfileComponent } from './profile/profile/profile.component';
import { PublicacionDetailComponent } from './publicaciones/publicacion-detail/publicacion-detail.component';
import { PublicacionesCrearComponent } from './publicaciones/publicaciones-crear/publicaciones-crear.component';
import { PublicacionesEmpresaComponent } from './publicaciones/publicaciones-empresa/publicaciones-empresa.component';
import { PublicacionesComponent } from './publicaciones/publicaciones.component';
import { PublicacionesMVPComponent } from './mvp/publicaciones/publicaciones.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { IndexComponent } from './_index/index/index.component';
import { ProductosComponent } from './mvp/productos/productos.component';
import { ProductosCrearComponent } from './mvp/productos/productos-crear/productos-crear.component';
import { ProductosVerComponent } from './mvp/productos/productos-ver/productos-ver.component';
import { DestacadasComponent } from './publicaciones/destacadas/destacadas.component';
import { DestacadoCrearComponent } from './publicaciones/destacadas/destacado-crear/destacado-crear.component';
import { SeccionesComponent } from './publicaciones/destacadas/secciones/secciones.component';
import { SeccionCrearComponent } from './publicaciones/destacadas/secciones/seccion-crear/seccion-crear.component';
import { SectoresComponent } from './mvp/sectores/sectores.component';
import { SectorCrearComponent } from './mvp/sectores/sector-crear/sector-crear.component';
import { EmpresasCreateComponent } from './empresas/empresas-create/empresas-create.component';
import { EditarSectorComponent } from './mvp/sectores/editar-sector/editar-sector.component';
import { SeccionEditarComponent } from './publicaciones/destacadas/secciones/seccion-editar/seccion-editar.component';
import { DestacadoEditarComponent } from './publicaciones/destacadas/destacado-editar/destacado-editar.component';
import { DestacadoVerComponent } from './publicaciones/destacadas/destacado-ver/destacado-ver.component';
import { EmpresaEditComponent } from './empresas/empresa-edit/empresa-edit.component';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('../pages/pages.module').then((m) => m.PagesModule),
      },
      {
        path: 'home',
        loadChildren: () => import('../pages/pages.module').then((m) => m.PagesModule),
      },
      {
        path: 'usuarios',
        component: UsuariosComponent,
      },
      {
        path: 'empresas',
        component: EmpresasComponent,
      },
      {
        path: 'empresas/create',
        component: EmpresasCreateComponent,
      },
      {
        path: 'empresas/editar/:id',
        component: EmpresaEditComponent,
      },
      {
        path: 'empresa/:id',
        component: EmpresaDetailComponent,
      },
      {
        path: 'consumer/:user',
        component: ConsumerDetailComponent,
      },
      {
        path: 'notificaciones',
        component: NotificacionesComponent,
      },
      {
        path: 'mvp',
        children: [
          {
            path: 'publicaciones',
            component: PublicacionesMVPComponent,
          },
          {
            path: 'productos',
            children: [
              {
                path: '',
                component: ProductosComponent,
              },

              {
                path: 'crear',
                component: ProductosCrearComponent,
              },
              {
                path: 'editar/:id',
                component: ProductosCrearComponent,
              },
              {
                path: ':id',
                component: ProductosVerComponent,
              },
            ],
          },
          {
            path: 'destacados',
            children: [
              {
                // post destacados
                path: '',
                component: DestacadasComponent,
              },
              {
                // crear nuevo post destacado
                path: 'crear',
                component: DestacadoCrearComponent,
              },
              {
                // editar post destacado
                path: 'editar/:id',
                component: DestacadoEditarComponent,
              },
              {
                // ver detalles post destacado
                path: 'ver/:id',
                component: DestacadoVerComponent,
              },
              {
                // secciones destacadas
                path: 'seccion',
                component: SeccionesComponent,
              },
              {
                // crear nueva sección destacada
                path: 'seccion/crear',
                component: SeccionCrearComponent,
              },
              {
                // editar sección destacada
                path: 'seccion/editar/:id',
                component: SeccionEditarComponent,
              },
            ],
          },
          {
            path: 'sector',
            children: [
              {
                path: '',
                component: SectoresComponent,
              },
              {
                path: 'crear',
                component: SectorCrearComponent,
              },
              {
                path: 'editar/:id',
                component: EditarSectorComponent,
              },
            ],
          },
        ],
      },
      {
        path: 'publicaciones',
        children: [
          {
            path: 'crear',
            component: PublicacionesCrearComponent,
          },
          {
            path: 'editar/:id',
            component: PublicacionesCrearComponent,
          },
          {
            path: 'empresa/:empresa',
            component: PublicacionesEmpresaComponent,
          },
          {
            path: ':id',
            component: PublicacionDetailComponent,
          },
          {
            path: '',
            component: PublicacionesComponent,
          },
        ],
      },
      {
        path: 'dashboard',
        children: [
          {
            path: 'profile',
            children: [
              {
                path: 'detail',
                component: ProfileComponent,
              },
            ],
          },
        ],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PublicRoutingModule {}
