import { Component, OnInit } from "@angular/core";
import { NbIconLibraries } from "@nebular/theme";
import { MainService } from "../services/main.service";

@Component({
  selector: "ngx-empresas",
  templateUrl: "./empresas.component.html",
  styleUrls: ["./empresas.component.scss"],
})
export class EmpresasComponent implements OnInit {

  /** API URLS */
  readonly API_EMPRESAS = "api/empresa";
  
  /** Kpi urls */
  readonly API_EMPRESAS_REGISTRADAS = "api/empresa/count";
  readonly API_EMPRESAS_ACTIVAS = "api/empresa/active/count";

  /** Controller for loading */
  loading = false;

  /** Kpi's */
  registeredCompanies = 0;
  activeCompanies = 0;

  /** Object containing all related to empresas */
  empresas = {
    approved: {
      settings: {
        noDataMessage: "Ningún elemento encontrado.",
        actions: {
          columnTitle: "Acciones",
          filter: true,
          add: false,
          edit: false,
          delete: false,
          custom: [
            {
              name: "publicaciones",
              title:
                '<i class="fas fa-search" title="Ver publicaciones"></i>',
            },
            {
              name: "ver",
              title: '<i class="fas fa-pen" title="Ver empresa"></i>',
            },
            {
              name: "eliminar",
              title: '<i class="fas fa-trash" title="Eliminar empresa"></i>',
            },
          ],
        },
        columns: {
          nombre: { title: "Nombre", width: "80%" },
        },
        mode: "external",
      },
      source: [],
    },
    pending: {
      settings: {
        noDataMessage: "Ningún elemento pendiente.",
        actions: {
          columnTitle: "Acciones",
          filter: true,
          add: false,
          edit: false,
          delete: false,
          custom: [
            {
              name: "ver",
              title: '<i class="fa fa-search" title="Ver empresa"></i>',
            },
            {
              name: "rechazar",
              title: '<i class="fa fa-times" title="Rechazar"></i>',
            },
            {
              name: "aprobar",
              title: '<i class="fa fa-check" title="Aprobar"></i>',
            },
          ],
        },
        columns: {
          nombre: { title: "Nombre", width: "80%" },
        },
        mode: "external",
      },
      source: [],
    },
  };

  constructor(private mainService: MainService,
    private iconLibraries: NbIconLibraries) {
      this.iconLibraries.registerFontPack("font-awesome", {
        iconClassPrefix: "fa",
        packClass: "fas",
      });
    }

  ngOnInit(): void {
    this.fetchEmpresas();
    this.fetchKpis();
  }

  /**
   * Fetchs empresas from backend
   */
  fetchEmpresas() {
    this.loading = true;
    this.mainService
      .get({ api: this.API_EMPRESAS, hide404: true })
      .then((value) => {
        this.setEmpresasTable({ data: value });
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Sets and organizes approved and pending empresas data in tables
   * @param raw data from backend
   */
  setEmpresasTable({ data }) {
    this.empresas.approved.source = data.filter((e) => {
      if (e.estado === "aprobado") {
        return true;
      } else {
        return false;
      }
    });
    this.empresas.pending.source = data.filter((e) => {
      if (e.estado === "pendiente") {
        return true;
      } else {
        return false;
      }
    });
  }

  /** Sets kpi cards values */
  fetchKpis(){
    this.mainService.get({ api: this.API_EMPRESAS_ACTIVAS}).then((value) => {
      this.activeCompanies = value
    });

    this.mainService.get({api: this.API_EMPRESAS_REGISTRADAS }).then((value) => {
      this.registeredCompanies = value
    })
  }
}
