import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "../../services/auth.service";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";
import { S3Service } from "../../services/s3.service";

@Component({
	selector: "ngx-empresas-create",
	templateUrl: "./empresas-create.component.html",
	styleUrls: ["./empresas-create.component.scss"],
})
export class EmpresasCreateComponent implements OnInit {
	/* Binding */
	public empresa: any = {};
	public sectores = [];
	public selectedFile;
	public loading: boolean = false;

	/** API */
	private readonly API_SECTOR: string = "api/sector";
	private readonly API_EMPRESA: string = "api/empresa";

	/** Boolean to view if it is edit */
	public edit = false;

	constructor(
		private authService: AuthService,
		private mainService: MainService,
		private generalService: GeneralService,
		private s3: S3Service,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit() {
		const id = this.activatedRoute.snapshot.paramMap.get("id");
		if (id) {
			this.edit = true;
			this.fetchEmpresa({ id });
		}
		this.getSectores();
	}

	// =================================
	// Metodos
	// ================================

	/**
	 * Fetchs sectores from backend
	 */
	public getSectores() {
		this.mainService.get({ api: this.API_SECTOR }).then((response) => {
			this.sectores = response as [];
		});
	}

	/**
	 * Fetchs company from backend
	 * @param id
	 */
	fetchEmpresa({ id }): void {
		this.loading = true;
		this.mainService
			.get({ api: `${this.API_EMPRESA}/${id}` })
			.then((value) => {
				this.empresa = value;
				this.empresa.sectores = this.empresa.sectores.map((sector) => {
					return sector._id;
				});
			})
			.finally(() => {
				this.loading = false;
			});
	}

	/**
	 * Registers the empresa and the user
	 */
	public onSubmit(): void {
		const callback = (err, data) => {
			if (err) {
				// If there is an error alert the user
				alert("No fue posible subir el archivo, por favor intente más tarde");
			} else {
				const imgUrl = data.Location;
				this.register({ logo: imgUrl });
			}
		};
		if (this.selectedFile) {
			this.uploadFile({ file: this.selectedFile, callback });
		} else {
			this.register({ logo: this.empresa.logo });
		}
	}

	public register({ logo }) {
		if (
			this.empresa.nombre &&
			this.empresa.sectores &&
			this.empresa.descripcion
		) {
			if (this.edit) {
				this.empresa["logo"] = logo ? logo : "";
				this.mainService
					.put({
						api: `${this.API_EMPRESA}/${this.empresa._id}`,
						data: this.empresa,
					})
					.then((resEmpresa) => {
						this.generalService.handleSuccess({ text: "Empresa editada" });
					});
			} else {
				this.empresa["logo"] = logo ? logo : "";
				this.mainService
					.post({ api: this.API_EMPRESA, data: this.empresa })
					.then((resEmpresa) => {
						this.generalService.handleSuccess({ text: "Empresa creada" });
					});
			}
		} else {
			this.generalService.handleError({
				error: "Por favor llena todos los campos",
				titulo: "Error",
			});
		}
	}

	/**
	 * Opens logo file selector
	 */
	public openFileSelector() {
		document.getElementById("logo-selector").click();
	}

	/**
	 * Listens on change of select file
	 * @param param0 with event of the input
	 */
	public onChangeMedia({ event }) {
		if (event.target.files.length > 0) {
			this.selectedFile = event.target.files[0];
		}
	}

	/**
	 * Uploads pictures to S3 in a folder
	 * @param file: File object to upload, callback function to execute
	 */
	async uploadFile({ file, callback }) {
		const empresaName = this.empresa.nombre.replace(" ", "-");
		const key = `empresa/${
			empresaName || "no-name"
		}/logo-${this.s3.generateKey()}`;
		await this.s3.uploadFile({ file, key, callback });
	}

	  /**
   * Navigates back
   */
		 goBack() {
			this.generalService.goBack();
		}
}
