import { Component, ElementRef, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GeneralService } from '../../services/general.service';
import { MainService } from '../../services/main.service';
import { FormBuilder } from '@angular/forms';
import { NbDialogRef, NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-empresa-detail',
  templateUrl: './empresa-detail.component.html',
  styleUrls: ['./empresa-detail.component.scss'],
})
export class EmpresaDetailComponent implements OnInit {
  /** API URLS */
  readonly API_EMPRESA = 'api/empresa';
  private readonly API_SECTOR: string = 'api/sector';

  /** Empresa full object */
  empresa: any = {};

  /** Handles loader */
  loading = false;

  /** Array with sectores */
  public sectores: any = [];

  private dialogRefEmpresa: NbDialogRef<any>;

  @ViewChild('deleteEmpresaDialogFromDetail', { static: true }) deleteEmpresaDialogFromDetail: any;

  constructor(
    private mainService: MainService,
    private activatedRoute: ActivatedRoute,
    private generalService: GeneralService,
    private dialogService: NbDialogService
  ) {}

  ngOnInit(): void {
    this.getSectores();
  }

  /**
   * Fetchs company from backend
   * @param id
   */
  fetchEmpresa({ id }): void {
    this.loading = true;
    this.mainService
      .get({ api: `${this.API_EMPRESA}/${id}` })
      .then((value) => {
        this.empresa = value;
        this.empresa.sectores = this.empresa.sectores.map((sector) => {
          return { sector: this.sectores.find((value) => value._id === sector._id) };
        });
      })
      .finally(() => {
        this.loading = true;
      });
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }

  /** Navigates to edit sreen */
  public onEdit() {
    this.generalService.routerNavigateTo({
      path: `index/empresas/editar/${this.empresa._id}`,
    });
  }

  /**
   * Fetchs sectores from backend
   */
  public getSectores() {
    this.mainService
      .get({ api: this.API_SECTOR })
      .then((response) => {
        this.sectores = response as [];
      })
      .finally(() => {
        const id = this.activatedRoute.snapshot.paramMap.get('id');
        this.fetchEmpresa({ id });
      });
  }

  /**
   * Approves company
   * @param object with company id
   */
  approveEmpresa(): void {
    const id = this.empresa._id;
    this.loading = true;
    const newData = { estado: 'aprobado' };
    this.mainService
      .put({ api: `${this.API_EMPRESA}/${id}`, data: newData })
      .then((value) => {
        if (value) {
          this.fetchEmpresa({ id: value._id });
          this.generalService.showToastr({
            title: 'Empresa aprobada',
            message: ``,
            status: 'success',
          });
        }
        this.goBack();
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /** Navigates to Post by company */
  goToPosts() {
    this.generalService.routerNavigateTo({
      path: `/index/publicaciones/empresa/${this.empresa._id}`,
    });
  }

  openDialog(deleteEmpresaDialog: TemplateRef<any>) {
    this.dialogRefEmpresa = this.dialogService.open(this.deleteEmpresaDialogFromDetail, {
      context: {
        text: `${this.empresa.nombre}`,
      },
    });
  }

  /** Delete company */
  deleteEmpresa(ref) {
    const id = this.empresa._id;
    this.mainService
      .delete({
        api: `${this.API_EMPRESA}/${id}`,
      })
      .finally(() => {
        this.dialogRefEmpresa.close();
        this.goBack();
      });
  }
}
