import { Component, OnInit, TemplateRef } from '@angular/core';
import { NbDialogRef, NbDialogService } from '@nebular/theme';
import { LocalDataSource } from 'ng2-smart-table';
import { GeneralService } from '../../services/general.service';
import { MainService } from '../../services/main.service';

@Component({
  selector: 'ngx-sectores',
  templateUrl: './sectores.component.html',
  styleUrls: ['./sectores.component.scss'],
})
export class SectoresComponent implements OnInit {
  readonly API_SECTOR = 'api/sector';
  readonly API_SECTOR_USERS = 'api/sector/users';

  public sectores: any = [];
  public source: LocalDataSource;
  public settings: any = {};

  private dialogRef: NbDialogRef<any>;

  constructor(
    private mainService: MainService,
    private generalService: GeneralService,
    private dialogService: NbDialogService
  ) {}

  ngOnInit(): void {
    this.getSectores();
    this.setSettings();
  }

  onDeleteSector(ref: any, id: string) {
    this.mainService.delete({ api: `${this.API_SECTOR}/${id}` }).then((response) => {
      this.getSectores();
      this.closeDialog(ref);
    });
  }

  closeDialog(ref: any) {
    ref.close();
  }

  public getSectores() {
    this.mainService.get({ api: this.API_SECTOR_USERS }).then((res) => {
      this.sectores = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.sectores);
    });
  }

  onCustomAction({ event }, deleteSectorDialog: TemplateRef<any>): void {
    switch (event.action) {
      case 'editar-sector':
        this.generalService.routerNavigateTo({
          path: `index/mvp/sector/editar/${event.data._id}`,
        });
        break;

      case 'eliminar-sector':
        this.dialogRef = this.dialogService.open(deleteSectorDialog, {
          context: {
            text: event.data.nombre,
            id: event.data._id,
          },
        });
        break;

      default:
        break;
    }
  }

  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/sector/crear`,
    });
  }

  public setAttributes() {
    this.sectores = this.sectores.map((sector) => {
      return {
        total: sector.users.length,
        nombre: sector.nombre,
        icono: sector.icono,
        descripcion: sector.descripcion,
        _id: sector._id,
      };
    });
  }

  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: 'Acciones',
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: 'editar-sector',
            title: '<i title="Editar sector" class="fas fa-pen"></i>',
          },
          {
            name: 'eliminar-sector',
            title: '<i title="Eliminar sector" class="fas fa-trash"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: 'Nombre' },
        descripcion: {
          title: 'Descripcion',
          valuePrepareFunction: (value: string) => {
            return value[0];
          },
        },
        total: {
          title: 'Total usuarios',
          filter: false,
        },
        icono: {
          title: 'Icono',
          filter: false,
          type: 'html',
          valuePrepareFunction: (images: string) => {
            return `<img class='table-thumbnail-img' src='${images}'/>`;
          },
        },
      },
      mode: 'external',
    };
  }
}
